nav {
  min-height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  button {
    background: transparent;
    border: 2px solid rgb(65,65,65);
    cursor: pointer;
    padding: .5rem;
    transition: all .3s ease;
    &:hover {
      background: rgb(65,65,65);
      color: #ffffff;
    }
  }
}