@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box;scrollbar-width:thin;scrollbar-color:rgba(155,155,155,0.5) transparent;font-family:'Lato', sans-serif}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background-color:rgba(155,155,155,0.5);border-radius:20px;border:transparent}h1,h2,h3{color:#363636}h3,h4{font-weight:400;color:#646464}.App{transition:all .5s ease}.library-active{margin-left:30%}.song-container{min-height:60vh;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.song-container img{width:20%;border-radius:50%}.song-container h2{padding:3rem 1rem 1rem 1rem}.song-container h3{font-size:1rem}.player{min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.time-control{width:50%;display:flex}.time-control input{cursor:pointer;width:100%;padding:1rem 0;-webkit-appearance:none;background:transparent}.time-control input:focus{outline:none}.time-control input::-webkit-slider-thumb{-webkit-appearance:none;height:16px;width:16px;background-color:#000}.time-control p{padding:1rem}.play-control{display:flex;justify-content:space-between;align-items:center;padding:1rem;width:30rem}.play-control svg{cursor:pointer}.track{width:100%;height:1rem;position:relative;border-radius:1rem;overflow:hidden;background-color:aqua}.animate-track{background:#ccc;width:100%;height:100%;position:absolute;top:0;left:0;transform:translateX(0);pointer-events:none}.library{position:fixed;top:0;left:0;width:20rem;height:100%;box-shadow:2px 2px 50px #cac8c8;overflow-y:scroll;transform:translateX(-100%);transition:all .3s ease;opacity:0}.library h2{padding:2rem}.library-song{display:flex;padding:1rem 2rem;cursor:pointer;transition:background-color .3s}.library-song img{width:30%}.library-song:hover{background-color:rgba(200,200,200,0.2)}.song-info{padding-left:1rem}.song-info h3{font-size:1rem}.song-info h4{font-size:.75rem}.selected{background-color:#d1fdf9}.active-library{transform:translateX(0);opacity:1}nav{min-height:10vh;display:flex;justify-content:space-around;align-items:center}nav button{background:transparent;border:2px solid #414141;cursor:pointer;padding:.5rem;transition:all .3s ease}nav button:hover{background:#414141;color:#ffffff}@media screen and (max-width: 768px){.song-container img{width:60%}.time-control{width:90%}.play-control{width:60%}.nav button{z-index:10}.library{width:100%}}

